footer {
  background-color: #363636;
  color: #fff;
  padding: 20px;
  text-align: center;
  padding-top: 20px;
  -webkit-box-shadow: inset 0px 28px 12px -22px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: inset 0px 28px 12px -22px rgba(0, 0, 0, 0.75);
  box-shadow: inset 0px 28px 12px -22px rgba(0, 0, 0, 0.75);
  position: relative;
  z-index: 2;
  margin-top: 50px;
  div {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;

    address {
      font-style: normal;
      margin-bottom: 10px;
    }

    a {
      text-decoration: none;
      color: #fff;
      display: flex;
      align-items: center;
      transition: 0.2s;
    }

    a {
      p {
        margin-left: 5px;
        background-image: radial-gradient(
          circle at 30% 107%,
          #fdf497 0%,
          #fdf497 5%,
          #fd5949 45%,
          #d6249f 60%,
          #285aeb 90%
        );
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }

      svg {
        fill: #fd664f;
      }
    }
  }

  span {
    color: gray;
    font-size: 0.9rem;
    display: block;
  }
}
